<template>
  <div>
    <el-container direction="vertical">
      <el-row class="top-bar">
        <img src="../../assets/ijob.png" class="image" />
        <el-col :span="3" :offset="2">
          <div class="headText1" @click="backToMain">
            <h4></h4>
          </div>
        </el-col>
        <el-col :span="3">
          <div class="headText2">
            <h6>by 1.2</h6>
          </div>
        </el-col>
        <el-col :span="2">
          <div class="headText2">
            <h6>Welcome: {{this.Global.loginid}}</h6>
          </div>
        </el-col>
        <el-col :span="1" :offset="10">
          <el-button id="exit" type="primary" @click="backToLogin"></el-button>
        </el-col>
      </el-row>

      <div class="line"></div>
      <el-menu
        :default-active="$route.path"
        class="menu"
        mode="horizontal"
        @select="handleSelect"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        router
      >
        <el-menu-item index="/User/Enterprice" ><i class="el-icon-setting"></i></el-menu-item>

        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-location"></i>
          </template>
          <el-menu-item index="/User/NewPos"></el-menu-item>
          <el-menu-item index="/User/ManPos"></el-menu-item>
        </el-submenu>
        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-menu"></i>
          </template>

          <el-menu-item index="/User/NewTalk"></el-menu-item>
          <el-menu-item index="/User/ManTalk"></el-menu-item>
        </el-submenu>
        <el-menu-item index="/User/ResSer" ><i class="el-icon-document"></i></el-menu-item>
      </el-menu>
      <el-row class="tac"></el-row>

      <el-dialog title="" :visible.sync="companyDialogFormVisible">
        <el-form ref="companyForm" :model="companyForm">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="companyForm.cname" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="companyForm.industry" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="scale" :label-width="formLabelWidth">
            <el-select v-model="companyForm.scale" placeholder="">
              <el-option label="X<10" value=""></el-option>
              <el-option label="10X<100" value=""></el-option>
              <el-option label="100X<300" value=""></el-option>
              <el-option label="X300" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="companyForm.place" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-date-picker v-model="companyForm.registerDate" type="date" placeholder=""  value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="companyForm.registerCapital" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-date-picker v-model="companyForm.timebegin" type="date" placeholder="" value-format="yyyy-MM-dd"></el-date-picker>
            <el-date-picker v-model="companyForm.timeend" type="date" placeholder="" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="ctel" :label-width="formLabelWidth">
            <el-input v-model="companyForm.ctel" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="companyForm.cemail" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="companyForm.business" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="companyDialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="companyDialogFormVisible = false"> </el-button>
        </div>
      </el-dialog>

      <el-card class="basic">
        <p id="title"></p>

        <el-divider></el-divider>
        <p id="content">{{companyForm.cname}}</p>
        <p id="content">{{companyForm.industry}}</p>
        <p id="content">{{companyForm.scale}}</p>
        <p id="content">{{companyForm.place}}</p>
        <p id="content">{{companyForm.registerDate}}</p>
        <p id="content">{{companyForm.registerCapital}}</p>
        <p id="content">{{companyForm.timebegin}}  {{companyForm.timeend}}</p>
        <p id="content">{{companyForm.ctel}}</p>
        <p id="content">{{companyForm.cemail}}</p>
        <p id="content">{{companyForm.business}}</p>
      </el-card>
      <el-row color:transparent class="button">
        <el-col :span="1" :offset="0">
          <el-button round icon="el-icon-edit" @click="companyDialogFormVisible = true"></el-button>
        </el-col>
      </el-row>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "companyForm",
  data() {
    return {
      activeIndex: "/User/Enterprice",
      companyDialogFormVisible: false,
      companyForm: {
        cname: "",
        industry: "",
        scale: "X300",
        place: "",
        registerDate: "1998-11-11",
        registerCapital: "6500",
        timebegin: "1998-11-11",
        timeend: "2028-11-11",
        cemail: "service@tencent.com",
        ctel: "83765566",
        business: ""
      },
      created() {
        //this.showcompanyForm();
      },
      formLabelWidth: "100px"
    };
  },

  methods: {
    addcompanyForm() {
      this.$axios
        .get(this.HOME + "/api/add_companyForm", {
          params: {
            cname: this.companyForm.cname,
            industry: this.companyForm.industry,
            scale: this.companyForm.scale,
            place: this.companyForm.place,
            registerDate: this.companyForm.registerDate,
            registerCapital: this.companyForm.registerCapital,
            timebegin: this.companyForm.timebegin,
            timeend: this.companyForm.timeend,
            business: this.companyForm.business,
            ctel: this.companyForm.ctel,
            cemail: this.companyForm.cemail
          }
        })
        .then(response => {
          // var res = JSON.parse(response.bodyText);
          if (response.data.error_num === 0) {
            this.companyDialogFormVisible = false;
            this.showcompanyForm();
          } else {
            this.$message.error("");
            console.log(response.data.msg);
          }
        });
    },
    showcompanyForm() {
      this.$axios
        .get(this.HOME + "/api/show_companyForm", {
          params: {
            sloginid: this.Global.loginid
          }
        })
        .then(response => {
          // var res = JSON.parse(response.bodyText);
          if (response.data.error_num === 0) {
            this.companyForm.cname = response.data.cname;
            this.companyForm.industry = response.data.industry;
            this.companyForm.scale = response.data.scale;
            this.companyForm.place = response.data.place;
            this.companyForm.registerDate = response.data.registerDate;
            this.companyForm.registerCapital = response.data.registerCapital;
            this.companyForm.timebegin = response.data.timebegin;
            this.companyForm.timeend = response.data.timeend;
            this.companyForm.business = response.data.business;
            this.companyForm.ctel = response.data.ctel;
            this.companyForm.cemail = response.data.cemail;
            //this.$router.push("/Home");
          } else {
            this.$message.error("");
            console.log(response.data.msg);
          }
        });
    },
    // 
    backToMain() {
      this.$router.push({ path: "/User/Enterprice" });
    },
    backToLogin() {
      this.$router.push({ path: "/" });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>

<style lang="scss" scoped>
.top-bar {
  background: #646e77;
  color: white;
  a {
    color: white;
  }
  height: 60px;
}
.image {
  position: absolute;
  width: 100px;
  height: 40px;
  left: 10px;
  top: 10px;
  display: block;
}
.headText1 {
  position: relative;
  top: -8px;
}
.headText2 {
  position: relative;
  top: -5px;
}
#exit {
  position: relative;
  top: 15px;
  background-color: transparent;
  border: 0;
}
.menu {
  padding-left: 20%;
  padding-right: 20%;
}

.basic {
  width: 50%;
  margin-top: 40px;
  margin-bottom: 20px;
  margin-left: 20%;
  margin-right: 20%;
  padding-left: 5%;
  padding-right: 5%;
}
.button {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 45%;
}

.el-dialog {
  width: 500px;
  padding-right: 50px;
}
#title {
  font-size: 20px;
}
#content {
  font-size: 15px;
}
</style>



