<template>
  <el-container direction="vertical">
    <el-row class="top-bar">
       <img src="../../assets/ijob.png" class="image" />
      <el-col :span="3" :offset="2">
        <div class="headText1" @click="backToMain()">
          <h4></h4>
        </div>
      </el-col>
      <el-col :span="3">
        <div class="headText2">
          <h6>by 1.2</h6>
        </div>
      </el-col>
      <el-col :span="2">
        <div class="headText2">
          <h6>Welcome: {{this.Global.loginid}}</h6>
        </div>
      </el-col>
      <el-col :span="1" :offset="10">
        <el-button id="exit" type="primary" @click="backToLogin()"></el-button>
      </el-col>
    </el-row>

    <el-menu
      :default-active="$route.path"
      class="menu"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
      router
    >
   <el-menu-item index="/Student/Home" ><i class="el-icon-s-home"></i></el-menu-item>
      <el-menu-item index="/Student/Resume" ><i class="el-icon-document"></i></el-menu-item>
      <el-submenu index="3">
        <template slot="title" ><i class="el-icon-s-custom"></i> </template>
        <el-menu-item index="/Student/MyJob"></el-menu-item>
        <el-menu-item index="/Student/MyTalk"></el-menu-item>
      </el-submenu>
    </el-menu>

    <el-card id="jobDetail">
      <p id="title">{{talkDetail.sname}}</p>
      <p id="content">{{talkDetail.cname}}</p>
      <p id="content">{{talkDetail.splace}}</p>
      <p id="content">{{talkDetail.sdate}} {{talkDetail.sbegin}}-{{talkDetail.send}}</p>
      <p id="content">{{talkDetail.stheme}}</p>
      <p id="content">{{talkDetail.sactivity}}</p>
      <el-divider></el-divider>
      <p id="content"></p>
      <p id="content">{{talkDetail.scontent}}</p>
      <el-button
        id="deliver"
        type="warning"
        icon="el-icon-message"
        disabled
      ></el-button>
    </el-card>
    <el-card id="companyInfo">
      <p id="title2">{{companyInfo.cname}}</p>
      <el-divider></el-divider>
      <p id="content2">{{companyInfo.industry}}</p>
      <p id="content2">{{companyInfo.scale}}</p>
      <p id="content2">{{companyInfo.place}}</p>
      <p id="content2">{{companyInfo.registerDate}}</p>
      <p id="content2">{{companyInfo.registerCapital}}</p>
      <p id="content2">{{companyInfo.timeLimit}}</p>
      <p id="content2">{{companyInfo.business}}</p>
    </el-card>
  </el-container>
</template>

<script>
export default {
  name: "TalkDetail",
  data() {
    return {
      talkDetail: {
        seminarid: "1",
        sname: "",
        cname: "",
        splace: "101",
        sdate: "2020-05-15",
        sbegin: "10:30",
        send: "11:30",
        stheme: "",
        sactivity: "",
        scontent: "balabala"
      },
      companyInfo: {
        cname: "",
        industry: "IT",
        scale: "500",
        place: "",
        registerDate: "201611",
        registerCapital: "5000",
        timeLimit: "20161101-20461031",
        business:
          ""
      }
    };
  },
  created() {
    this.talkDetail.seminarid = this.$route.query.seminarid;
    this.showTalk(this.talkDetail.seminarid);
    this.showUser(this.talkDetail.seminarid);
  },
  methods: {
    showTalk(id) {
      this.$axios
        .get(this.HOME + "/api/show_talk", {
          params: {
            sloginid: this.Global.loginid,
            seminarid: this.talkDetail.seminarid
          }
        })
        .then(response => {
          this.talkDetail.sname = response.data.sname;
          this.talkDetail.cname = response.data.cname;
          this.talkDetail.splace = response.data.splace;
          this.talkDetail.cname = response.data.cname;
          this.talkDetail.sdate = response.data.sdate;
          this.talkDetail.sbegin = response.data.sbegin;
          this.talkDetail.send = response.data.send;
          this.talkDetail.stheme = response.data.stheme;
          this.talkDetail.sactivity = response.data.sactivity;
          this.talkDetail.scontent = response.data.scontent;
        });
    },
    showUser(id) {
      this.$axios
        .get(this.HOME + "/api/show_company", {
          params: {
            seminarid: this.talkDetail.jobid
          }
        })
        .then(response => {
          this.companyInfo.cname = response.data.cname;
          this.companyInfo.industry = response.data.industry;
          this.companyInfo.scale = response.data.scale;
          this.companyInfo.place = response.data.caddress;
          this.companyInfo.registerDate = response.data.registerDate;
          this.companyInfo.registerCapital = response.data.registerCapital;
          this.companyInfo.timeLimit = response.data.timeLimit;
          this.companyInfo.business = response.data.business;
        });
    },

    backToMain() {
      this.$router.push({ path: "/Student/Home" });
    },
    backToLogin() {
      this.Global.loginid = "";
      this.$router.push({ path: "/" });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>

<style lang="scss" scoped>
.top-bar {
  background: #646e77;
  color: white;
  a {
    color: white;
  }
  height: 60px;
}
.image {
  position: absolute;
  width: 100px;
  height: 40px;
  left: 10px;
  top: 10px;
  display: block;
}
.headText1 {
  position: relative;
  top: -8px;
}
.headText2 {
  position: relative;
  top: -5px;
}
#exit {
  position: relative;
  top: 15px;
  background-color: transparent;
  border: 0;
}
.menu {
  padding-left: 20%;
  padding-right: 20%;
}
#jobDetail {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 20%;
  margin-right: 41%;
}
#companyInfo {
  position: absolute;
  top: 120px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 60%;
  margin-right: 20%;
}
#deliver {
  position: absolute;
  top: 185px;
  left: 600px;
}
#title {
  font-size: 25px;
}
#content {
  font-size: 15px;
}
#title2 {
  font-size: 15px;
}
#content2 {
  font-size: 12px;
}
</style>



