<template>
  <div>
    <el-container direction="vertical">
      <el-row class="top-bar">
        <img src="../../assets/ijob.png" class="image" />
        <el-col :span="3" :offset="2">
          <div class="headText1" @click="backToMain">
            <h4></h4>
          </div>
        </el-col>
        <el-col :span="3">
          <div class="headText2">
            <h6>by 1.2</h6>
          </div>
        </el-col>
        <el-col :span="2">
          <div class="headText2">
            <h6>Welcome: {{this.Global.loginid}}</h6>
          </div>
        </el-col>
        <el-col :span="1" :offset="10">
          <el-button id="exit" type="primary" @click="backToLogin"></el-button>
        </el-col>
      </el-row>

      <div class="line"></div>
      <el-menu
        :default-active="$route.path"
        class="menu"
        mode="horizontal"
        @select="handleSelect"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        router
      >
        <el-menu-item index="/User/Enterprice">
          <i class="el-icon-setting"></i>
        </el-menu-item>

        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-location"></i>
          </template>
          <el-menu-item index="/User/NewPos"></el-menu-item>
          <el-menu-item index="/User/ManPos"></el-menu-item>
        </el-submenu>
        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-menu"></i>
          </template>

          <el-menu-item index="/User/NewTalk"></el-menu-item>
          <el-menu-item index="/User/ManTalk"></el-menu-item>
        </el-submenu>
        <el-menu-item index="/User/ResSer">
          <i class="el-icon-document"></i>
        </el-menu-item>
      </el-menu>
      <el-card class="pos">
        <el-table :data="tableData" style="width: 900px">
          <el-table-column label="" width="180" prop="jname"></el-table-column>
          <el-table-column label="" width="100" prop="salary"></el-table-column>
          <el-table-column label="" width="100" prop="jplace"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                @click="showResumeList(scope.$index, scope.row)"
              ></el-button>
            </template>
          </el-table-column>

          <el-table-column label="">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, tableData)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-dialog title="" :visible.sync="dialogFormVisible" width="40%">
          <el-form :model="editObj">
            <el-form-item label="">
              <el-input v-model="editObj.jname" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editObj.jplace" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editObj.salary" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editObj.detail" type="textarea" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editObj.requirement" type="textarea" auto-complete="off"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
          </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="resumeListVisible" width="40%">
          <el-table :data="resumeData" style="width: 100%">
            <el-table-column prop="sname" label="" width="100"></el-table-column>
            <el-table-column prop="school" label="" width="180"></el-table-column>
            <el-table-column prop="major" label="" width="180"></el-table-column>
            <el-table-column label="" width="100">
              <template v-slot="scope">
                <el-button type="primary" @click="showResumeContent(scope.row.content)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <span slot="footer" class="dialog-footer">
            <el-button @click="resumeListVisible = false"> </el-button>
            <el-button type="primary" @click="resumeListVisible = false"> </el-button>
          </span>
        </el-dialog>
        <el-dialog title="" :visible.sync="resumeContentVisible" width="40%">
          <h4></h4>
          <p>{{resumeContent.stel}}</p>
          <p>{{resumeContent.email}}</p>
          <h4></h4>
          <p>{{resumeContent.school}}</p>
          <p>{{resumeContent.major}}</p>
          <p>{{resumeContent.eduBegin}}</p>
          <p>{{resumeContent.grade}}</p>
          <h4></h4>
          <p>{{resumeContent.cname}}</p>
          <p>{{resumeContent.industry}}</p>
          <p>{{resumeContent.pname}}</p>
          <p>{{resumeContent.place}}</p>
          <p>{{resumeContent.expBegin}}</p>
          <p>{{resumeContent.detail}}</p>
          <h4></h4>
          <p>{{resumeContent.skill}}</p>
          <h4></h4>
          <p>{{resumeContent.self}}</p>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="qualify()"></el-button>
            <el-button type="danger" @click="resumeContentVisible = false"></el-button>
          </span>
        </el-dialog>
      </el-card>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      resumeContentVisible: false,
      resumeListVisible: false,
      dialogFormVisible: false,
      resumeContent: {},
      resumeData: [
        {
          sname: "",
          school: "",
          major: "",
          content: {
            stel: "15201967357",
            email: "772918068@qq.com",
            school: "",
            major: "",
            eduBegin: "2017.09",
            grade: "",
            cname: "",
            industry: "",
            pname: "",
            place: "",
            expBegin: "2019.07--2019.08",
            detail: "ui",
            skill: "c++css+javascript,python",
            self: ""
          }
        }
      ],
      editObj: {
        jobid: "",
        jname: "",
        jplace: "",
        salary: "",
        detail: "",
        requirement: ""
      },
      tableData: [
        {
          jname: "",
          jplace: "",
          salary: "10000-15000/",
          detail: "",
          requirement: ""
        },
        {
          jname: "",
          jplace: "",
          salary: "",
          detail: "",
          requirement: ""
        }
      ],
      formLabelWidth: "600px"
    };
  },
  created() {
    // this.showManPosForm();
  },
  methods: {
    qualify() {
      this.$axios
        .get(this.HOME + "/api/interview", {
          params: {}
        })
        .then(response => {
          if (response.data.error_num === 0) {
            this.resumeContentVisible = false;
          } else {
            this.$message.error("");
            console.log(response.data.msg);
          }
        });
    },
    showResumeList(index, row) {
      this.resumeListVisible = true;
    },
    showResumeContent(data) {
      this.resumeContent = data;
      this.resumeContentVisible = true;
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    backToMain() {
      this.$router.push({ path: "/User/Enterprice" });
    },
    backToLogin() {
      this.$router.push({ path: "/" });
    },
    handleEdit(index, row) {
      this.dialogFormVisible = true;
      this.editObj.jname = row.jname;
      this.editObj.jplace = row.jplace;
      this.editObj.salary = row.salary;
      this.editObj.place = row.place;
      this.editObj.detail = row.detail;
      this.editObj.requirement = row.requirement;
    },
    handleDelete(index, rows) {
      rows.splice(index, 1);
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>

<style lang="scss" scoped>
.top-bar {
  background: #646e77;
  color: white;
  a {
    color: white;
  }
  height: 60px;
}
.headText1 {
  position: relative;
  top: -8px;
}
.headText2 {
  position: relative;
  top: -5px;
}
#exit {
  position: relative;
  top: 15px;
  background-color: transparent;
  border: 0;
}
.menu {
  padding-left: 20%;
  padding-right: 20%;
}

.basic {
  position: absolute;
  width: 50%;

  top: 20%;
  //bottom: 20px;
  left: 20%;
  //right: 20%;
  padding-left: 5%;
  padding-right: 5%;
}
.button {
  background: transparent;
  color: transparent;
  position: absolute;
  margin-top: 500px;
  margin-bottom: 500px;
  margin-left: 45%;
  padding-left: 0%;
  padding-right: 0%;
  padding-left: -5px;
  padding-right: 6%;
}
.image {
  position: absolute;
  width: 100px;
  height: 40px;
  left: 10px;
  top: 10px;
  display: block;
}
.city {
  position: relative;
  margin-top: 0px;
  //margin-bottom: 500px;
  left: -100px;
  padding-left: 100px;
  //padding-right: 0%;
  //padding-left:-5px;
  // padding-right: 6%;
}
.pos {
  width: 60%;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 15%;
  margin-right: 15%;
  padding-left: 5%;
  padding-right: 5%;
}
.el-dialog {
  width: 500px;
  padding-right: 50px;
}
#title {
  font-size: 20px;
}
#content {
  font-size: 15px;
}
</style>



