<template>
  <el-container direction="vertical">
    <el-row class="top-bar">
       <img src="../../assets/ijob.png" class="image" />
      <el-col :span="3" :offset="2">
        <div class="headText1" @click="backToMain()">
          <h4></h4>
        </div>
      </el-col>
      <el-col :span="3">
        <div class="headText2">
          <h6>by 1.2</h6>
        </div>
      </el-col>
      <el-col :span="2">
        <div class="headText2">
          <h6>Welcome: {{this.Global.loginid}}</h6>
        </div>
      </el-col>
      <el-col :span="1" :offset="10">
        <el-button id="exit" type="primary" @click="backToLogin()"></el-button>
      </el-col>
    </el-row>

    <el-menu
      :default-active="$route.path"
      class="menu"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
      router
    >
      <el-menu-item index="/Student/Home" ><i class="el-icon-s-home"></i></el-menu-item>
      <el-menu-item index="/Student/Resume" ><i class="el-icon-document"></i></el-menu-item>
      <el-submenu index="3">
        <template slot="title" ><i class="el-icon-s-custom"></i> </template>
        <el-menu-item index="/Student/MyJob"></el-menu-item>
        <el-menu-item index="/Student/MyTalk"></el-menu-item>
      </el-submenu>
    </el-menu>

    <el-card id="jobDetail">
      <p id="title">{{jobDetail.jname}}</p>
      <p id="content">{{jobDetail.salary}}</p>
      <p id="content">{{jobDetail.jplace}}</p>
      <el-divider></el-divider>
      <p id="content"></p>
      <p id="content">{{jobDetail.detail}}</p>
      <el-divider></el-divider>
      <p id="content"></p>
      <p id="content">{{jobDetail.requirement}}</p>
      <el-button id="deliver" type="warning" icon="el-icon-message" disabled></el-button>
    </el-card>
    <el-card id="companyInfo">
      <p id="title2">{{companyInfo.cname}}</p>
      <el-divider></el-divider>
      <p id="content2">{{companyInfo.industry}}</p>
      <p id="content2">{{companyInfo.scale}}</p>
      <p id="content2">{{companyInfo.place}}</p>
      <p id="content2">{{companyInfo.registerDate}}</p>
      <p id="content2">{{companyInfo.registerCapital}}</p>
      <p id="content2">{{companyInfo.timeLimit}}</p>
      <p id="content2">{{companyInfo.business}}</p>
    </el-card>
    <el-card id="timeline">
      <p id="content"></p>
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in activities"
          :key="index"
          :timestamp="activity.timestamp"
        >{{activity.content}}</el-timeline-item>
      </el-timeline>
    </el-card>
  </el-container>
</template>

<script>
export default {
  name: "JobDetail",
  data() {
    return {
      jobDetail: {
        jobid: "1",
        jname: "C++",
        salary: "300-400/",
        jplace: "",
        cname: "",
        detail:
          "",
        requirement:
          ""
      },
      companyInfo: {
        cname: "",
        industry: "IT",
        scale: "500",
        place: "",
        registerDate: "201611",
        registerCapital: "5000",
        timeLimit: "20161101-20461031",
        business:
          ""
      },
      activities: [
        {
          content: "",
          timestamp: "2020-01-01"
        }
      ]
    };
  },
  created() {
    this.jobDetail.jobid = this.$route.query.jobid;
    this.showJob(this.jobDetail.jobid);
    this.showUser(this.jobDetail.jobid);
  },
  methods: {
    showJob(id) {
      this.$axios
        .get(this.HOME + "/api/show_job", {
          params: {
            sloginid: this.Global.loginid,
            jobid: this.jobDetail.jobid
          }
        })
        .then(response => {
          this.jobDetail.jname = response.data.jname;
          this.jobDetail.salary = response.data.salary;
          this.jobDetail.jplace = response.data.jplace;
          this.jobDetail.cname = response.data.cname;
          this.jobDetail.detail = response.data.jcontent;
          this.jobDetail.requirement = response.data.jrequirement;
        });
    },
    showUser(id) {
      this.$axios
        .get(this.HOME + "/api/show_company", {
          params: {
            jobid: this.jobDetail.jobid
          }
        })
        .then(response => {
          this.companyInfo.cname = response.data.cname;
          this.companyInfo.industry = response.data.industry;
          this.companyInfo.scale = response.data.scale;
          this.companyInfo.place = response.data.caddress;
          this.companyInfo.registerDate = response.data.registerDate;
          this.companyInfo.registerCapital = response.data.registerCapital;
          this.companyInfo.timeLimit = response.data.timeLimit;
          this.companyInfo.business = response.data.business;
        });
    },
    backToMain() {
      this.$router.push({ path: "/Student/Home" });
    },
    backToLogin() {
      this.Global.loginid = "";
      this.$router.push({ path: "/" });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>

<style lang="scss" scoped>
.top-bar {
  background: #646e77;
  color: white;
  a {
    color: white;
  }
  height: 60px;
}
.image {
  position: absolute;
  width: 100px;
  height: 40px;
  left: 10px;
  top: 10px;
  display: block;
}
.headText1 {
  position: relative;
  top: -8px;
}
.headText2 {
  position: relative;
  top: -5px;
}
#exit {
  position: relative;
  top: 15px;
  background-color: transparent;
  border: 0;
}
.menu {
  padding-left: 20%;
  padding-right: 20%;
}
#jobDetail {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 20%;
  margin-right: 41%;
}
#companyInfo {
  position: absolute;
  top: 120px;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 60%;
  margin-right: 20%;
}
#timeline {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 20%;
  margin-right: 41%;
}
#deliver {
  position: absolute;
  top: 185px;
  left: 600px;
}
#title {
  font-size: 25px;
}
#content {
  font-size: 15px;
}
#title2 {
  font-size: 15px;
}
#content2 {
  font-size: 12px;
}
</style>



