<template>
  <el-container direction="vertical">
    <el-row class="top-bar">
       <img src="../../assets/ijob.png" class="image" />
      <el-col :span="3" :offset="2">
        <div class="headText1" @click="backToMain()">
          <h4></h4>
        </div>
      </el-col>
      <el-col :span="3">
        <div class="headText2">
          <h6>by 1.2</h6>
        </div>
      </el-col>
      <el-col :span="2">
        <div class="headText2">
          <h6>Welcome: {{this.Global.loginid}}</h6>
        </div>
      </el-col>
      <el-col :span="1" :offset="10">
        <el-button id="exit" type="primary" @click="backToLogin()"></el-button>
      </el-col>
    </el-row>

    <el-menu
      :default-active="$route.path"
      class="menu"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
      router
    >
   <el-menu-item index="/Student/Home" ><i class="el-icon-s-home"></i></el-menu-item>
      <el-menu-item index="/Student/Resume" ><i class="el-icon-document"></i></el-menu-item>
      <el-submenu index="3">
        <template slot="title" ><i class="el-icon-s-custom"></i> </template>
        <el-menu-item index="/Student/MyJob"></el-menu-item>
        <el-menu-item index="/Student/MyTalk"></el-menu-item>
      </el-submenu>
    </el-menu>

    <el-card class="search">
      <el-row type="flex" justify="center">
        <el-col :span="12">
          <el-input v-model="searchinput" placeholder=""></el-input>
        </el-col>
        <el-col :span="3">
          <el-select v-model="value" filterable placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" icon="el-icon-search" @click="search(searchinput)"></el-button>
        </el-col>
      </el-row>
      <el-row type="flex" justify="start">
        <div id="hotSearch">
          <p>{{hotSearch.first}} {{hotSearch.second}} {{hotSearch.third}}</p>
        </div>
      </el-row>
    </el-card>

    <div v-if="searchData.length>0">
      <el-card class="searchResult" v-for="(job, index) in searchData" :key="index">
        <el-link type="primary">{{job.jname}}</el-link>
        <p>
          <span>{{job.salary}}</span>
          <el-divider direction="vertical"></el-divider>
          <span>{{job.jplace}}</span>
          <el-divider direction="vertical"></el-divider>
          <span>{{job.cname}}</span>
        </p>
      </el-card>
    </div>
  </el-container>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      value: "",
      searchinput: "",
      hotSearch: {
        first: "java",
        second: "",
        third: ""
      },
      jobList: [
        {
          jname: "C++",
          salary: "300-400/",
          jplace: "",
          cname: ""
        },
        {
          jname: "",
          salary: "250-300/",
          jplace: "",
          cname: ""
        },
        {
          jname: "java",
          salary: "250-300/",
          jplace: "",
          cname: ""
        },
        {
          jname: "",
          salary: "150-200/",
          jplace: "",
          cname: ""
        }
      ], // 
      searchData: [] // 
    };
  },
  created() {
    this.searchinput = this.$route.query.searchinput;
    //getJobList();
    this.search(this.searchinput);
  },
  methods: {
    getJobList() {
      this.$axios
        .get(this.HOME + "/api/get_job", {
          params: {}
        })
        .then(response => {
          this.jobList = response.data;
        });
    },
    search(str) {
      if (this.value == "") {
        this.searchJob(str);
      } else if (this.value == "") {
        this.$router.push({
          path: "/Student/Search2",
          query: { searchinput: this.searchinput }
        });
      }
    },
    searchJob(str) {
      if (str) {
        this.searchData = this.jobList.filter(function(job) {
          return Object.keys(job).some(function(key) {
            // 
            return (
              String(job[key])
                .toLowerCase() // 
                .indexOf(str) > -1 // 
            );
          });
        });
      }
    },
    backToMain() {
      this.$router.push({ path: "/Student/Home" });
    },
    backToLogin() {
      this.Global.loginid = "";
      this.$router.push({ path: "/" });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>

<style lang="scss" scoped>
.top-bar {
  background: #646e77;
  color: white;
  a {
    color: white;
  }
  height: 60px;
}
.image {
  position: absolute;
  width: 100px;
  height: 40px;
  left: 10px;
  top: 10px;
  display: block;
}
.headText1 {
  position: relative;
  top: -8px;
}
.headText2 {
  position: relative;
  top: -5px;
}
#exit {
  position: relative;
  top: 15px;
  background-color: transparent;
  border: 0;
}
.menu {
  padding-left: 20%;
  padding-right: 20%;
}
.search {
  width: 60%;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 10%;
  margin-right: 10%;
}
.searchResult {
  width: 50%;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 20%;
  margin-right: 20%;
  padding-left: 5%;
  padding-right: 5%;
  font-size: 15px;
}
.el-link {
  font-size: 18px;
}
#hotSearch {
  font-size: 15px;
  position: relative;
  left: 10%;
}
.recommend {
  padding-left: 20%;
  padding-right: 20%;
}

.el-card {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 20%;
  margin-right: 20%;
}
</style>



